<template>
  <div>
    <p>Generate Java POJO File From Json</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <br>
    <div>
      <textarea v-model="json_schema" id="json_schema" rows="50" cols="100" placeholder="json schema area"></textarea>
      <textarea v-model="java_schema" id="java_schema" rows="50" cols="100" placeholder="java schema area" readonly></textarea>
    </div>
    <br>
    <div>
      <!-- button -->
      <button @click="format()">format</button>
      <button @click="generate()">generate</button>
      <button @click="clean()">clean</button>
    </div>
  </div>
</template>

<script>
/*
json example
    base java type
        {
            "name": "fjh",
            "age": 18,
            "score": 99.9,
            "isSuccess": true,
            "testNull": null
        }
    dto java type
        {
            "name": "fjh",
            "profile": {
                "profileId": "abc123",
                "profileDetail": {
                    "detailId": 123,
                    "content": "bbb"
                }
            }
        }
    map java type
        {
            "name": "fjh",
            "simpleMap": {
                "id1": 1,
                "id2": 2
            },
            "mapAndDto": {
                "dto1": {
                    "name": "fjh",
                    "age": 1
                }
            },
            "mapAndMap": {
                "map1": {
                    "name1": "fjh"
                }
            },
            "mapAndMapAndDto": {
                "map1": {
                    "name1": {
                        "name": "fjh",
                        "age": 1
                    }
                }
            }
        }
    list java type
        {
            "name": "fjh",
            "intList": [
                1,
                2
            ],
            "objList": [
                {
                    "name": "fjh",
                    "dtoList": [
                        "1"
                    ]
                }
            ],
            "mapList": [
                {
                    "id1": 1
                }
            ],
            "mapObjList": [
                {
                    "id1": {
                        "name": "fjh",
                        "age": 18
                    }
                }
            ],
            "mapObjListList": [
                [
                    {
                        "id2": {
                            "name": "fjh",
                            "address": 18
                        }
                    }
                ]
            ]
        }
*/

// Java Access Type
const privateAccessType = 'private'

// Java Type
const integerType = 'Integer'
const doubleType = 'Double'
const booleanType = 'Boolean'
const stringType = 'String'
const objectType = 'Object'

// have specify handle type
const listType = 'List'
const mapType = 'Map'
const dtoType = 'Dto'

// Java Class Constant Area
const classType = 'public class %0% {'
const javaLineEndSymbol = ';'
const javaClassFileEndSymbol = '\n}'
const javaTabSymbol = '    '
const singleClassSymbol = '\n\n'
const betweenWordSymbol = ' '
const noJavaFieldRegexp = /^(([a-z]+[0-9]+)|([0-9]+[a-z]+))[a-z0-9]*$/i

// global var
let handleStack
let replaceSymbol

// temp constant
const listFristIndex = 0

export default {
  name: 'GenerateJavaPojoView',
  data () {
    return {
      json_schema: '',
      java_schema: ''
    }
  },
  methods: {
    // clean record
    clean () {
      this.java_schema = ''
      this.json_schema = ''
    },
    // format json
    format () {
      try {
        const sourceText = this.json_schema
        this.json_schema = JSON.stringify(JSON.parse(sourceText), null, 4)
      } catch (e) {
        console.log(e)
        alert('illegal json schema')
      }
    },
    // replace placeholder function
    replace_placeholder (str, params) {
      if (arguments.length === 1) return arguments[0]
      const param = arguments[1]
      let s = arguments[0]
      if (typeof (param) === 'object') {
        for (const k in param) { s = s.replace(new RegExp('\\%' + k + '\\%', 'g'), param[k]) }
      } else {
        for (let i = 1; i < arguments.length; i++) { s = s.replace(new RegExp('\\%' + (i - 1) + '\\%', 'g'), arguments[i]) }
      }
      return s
    },
    // generate function
    generate () {
      try {
        const jsonSchemaText = this.json_schema
        const jsonSchemaObj = JSON.parse(jsonSchemaText)
        handleStack = []
        replaceSymbol = {}
        const rootType = this.getJavaClassType(jsonSchemaObj)
        handleStack.push({
          // default parent class name
          className: 'Root',
          obj: jsonSchemaObj,
          type: rootType
        })
        let javaSchemaText = ''
        let temp
        while (handleStack.length > 0) {
          temp = this.packageJavaObject(handleStack.pop())
          if (temp != null) {
            javaSchemaText = javaSchemaText + temp + singleClassSymbol
          }
        }
        this.java_schema = this.replaceTempSymbol(javaSchemaText)
      } catch (e) {
        console.log(e)
        alert('illegal json schema')
      }
    },
    // replace temp symbol
    replaceTempSymbol (javaSchemaText) {
      while (javaSchemaText.includes('%')) {
        javaSchemaText = this.replace_placeholder(javaSchemaText, replaceSymbol)
      }
      return javaSchemaText
    },
    // package type info
    packageTypeInfo (className, field, fieldType) {
      switch (fieldType) {
        case dtoType:
        case mapType:
        case listType:
          return this.createTypePlaceholder(className + field + fieldType)
        default:
          return fieldType
      }
    },
    // get java class type
    getJavaClassType (obj) {
      if (typeof (obj) === 'string') {
        return stringType
      } else if (typeof (obj) === 'boolean') {
        return booleanType
      } else if (typeof (obj) === 'number') {
        if (obj.toString().includes('.')) {
          return doubleType
        } else {
          return integerType
        }
      } else if (obj instanceof Array) {
        return listType
      } else if (obj == null || Object.keys(obj).length === 0) {
        return objectType
      } else {
        let isMap = false
        for (const innerField in obj) {
          if (!isNaN(Number(innerField))) {
            isMap = true
            break
          }
          if (noJavaFieldRegexp.test(innerField)) {
            isMap = true
            break
          }
        }
        return isMap ? mapType : dtoType
      }
    },
    // create type placeholder
    createTypePlaceholder (placeholder) {
      return '%' + placeholder + '%'
    },
    // push handle info
    pushHandleInfo (className, obj, type) {
      handleStack.push({
        className: className,
        obj: obj,
        type: type
      })
    },
    // uppercase frist element
    upperCaseFristElement (text) {
      return text.substring(0, 1).toUpperCase() + text.substring(1)
    },
    // create class name
    createClassName (className, field) {
      if (field === 0) {
        // list item class name
        return className + 'Item'
      } else {
        return this.upperCaseFristElement(field)
      }
    },
    // package map type placeholder
    packageMapTypePlaceholder (placeholder) {
      return 'Map<String, ' + placeholder + '>'
    },
    // package list type placeholder
    packageListTypePlaceholder (placeholder) {
      return 'List<' + placeholder + '>'
    },
    // analysis field info
    analysisFieldInfo (className, obj, field, fieldType, objType) {
      let fieldClassName
      let mapObj
      let listObj
      let fristKey
      let fristValueType
      switch (fieldType) {
        case dtoType:
          fieldClassName = this.createClassName(className, field) + fieldType
          replaceSymbol[className + field + fieldType] = fieldClassName
          this.pushHandleInfo(fieldClassName, obj[field], fieldType)
          break
        case mapType:
          fieldClassName = this.createClassName(className, field) + fieldType
          mapObj = obj[field]
          fristKey = Object.keys(mapObj)[0]
          fristValueType = this.getJavaClassType(mapObj[fristKey])
          replaceSymbol[className + field + fieldType] = this.packageMapTypePlaceholder(this.createTypePlaceholder(fieldClassName + fristKey + fristValueType))
          this.pushHandleInfo(fieldClassName, mapObj, fieldType)
          break
        case listType:
          fieldClassName = this.createClassName(className, field) + fieldType
          listObj = obj[field]
          fristValueType = this.getJavaClassType(listObj[listFristIndex])
          replaceSymbol[className + field + fieldType] = this.packageListTypePlaceholder(this.createTypePlaceholder(fieldClassName + listFristIndex + fristValueType))
          this.pushHandleInfo(fieldClassName, listObj, fieldType)
          break
        default:
          if (objType === mapType || objType === listType) {
            replaceSymbol[className + field + fieldType] = fieldType
          }
          break
      }
    },
    // package java object
    packageJavaObject ({ className, obj, type }) {
      let result = this.replace_placeholder(classType, className)
      let fieldType
      let fristKey
      let fristValueType
      switch (type) {
        case dtoType:
          for (const field in obj) {
            fieldType = this.getJavaClassType(obj[field])
            result = result + singleClassSymbol + javaTabSymbol + privateAccessType + betweenWordSymbol + this.packageTypeInfo(className, field, fieldType) + betweenWordSymbol + field + javaLineEndSymbol
            this.analysisFieldInfo(className, obj, field, fieldType, type)
          }
          return result + javaClassFileEndSymbol
        case mapType:
          // only get frist k-v
          fristKey = Object.keys(obj)[0]
          fristValueType = this.getJavaClassType(obj[fristKey])
          this.analysisFieldInfo(className, obj, fristKey, fristValueType, type)
          return null
        case listType:
          // only get frist element
          fristValueType = this.getJavaClassType(obj[listFristIndex])
          this.analysisFieldInfo(className, obj, listFristIndex, fristValueType, type)
          return null
      }
    }
  }
}
</script>

